import { createStore } from 'vuex'

export default createStore({
	state: {
		access_token: '',
		session_token: '',
		endpoint_type: '',
		run_mode: '',
		backend_type: '',
		group_id: 0,
		step: 0,
		skin: '',
		proxy_endpoint: '',
		proxy_scope: '',
		workflow_status: {
			actions: [],
			finished: false,
			internal: false,
			step: -1,
			stepState: 'before_init',
		},
		form_storage: {},
		form_errors: {},
		product_detail: {},
	},
	mutations: {
		access_token(state, payload) {
			state.access_token = payload.value;
		},
		session_token(state, payload) {
			state.session_token = payload.value;
		},
		endpoint_type(state, payload) {
			state.endpoint_type = payload.value;
		},
		backend_type(state, payload) {
			state.backend_type = payload.value;
		},
		run_mode(state, payload) {
			state.run_mode = payload.value;
		},
		group_id(state, payload) {
			state.group_id = payload.value;
		},
		step(state, payload) {
			state.step = payload.value;
		},
		skin(state, payload) {
			state.skin = payload.value;
		},
		proxy_endpoint(state, payload) {
			state.proxy_endpoint = payload.value;
		},
		proxy_scope(state, payload) {
			state.proxy_scope = payload.value;
		},
		workflow_status(state, payload) {
			state.workflow_status = payload.value;
		},
		form_storage(state, payload) {
			state.form_storage = payload.value;
		},
		form_errors(state, payload) {
			state.form_errors = payload.value;
		},
		product_detail(state, payload) {
			state.product_detail = payload.value;
		},
	},
	getters: {
	},
	actions: {
	},
	modules: {
	}
})
