<template>
	<select v-model="value" class="form-control" :id="field_id" :name="field_id" :disabled="disabled">
		<option v-for="(item, key) in field_data.enum" :value="option_value(item)">
			{{ option_label(item) }}
		</option>
	</select>
</template>

<script>
	import State_session_params from '@/store/State_session_params.js';

	export default {
		props: {
			field_id: String,
			field_data: Object,
		},
		components: {
		},
		data() {
			return {
				dynamic: [
					['auto_model', 'auto_znacka'],
				],
			}
		},
		created() {
		},
		methods: {
			// we have two types of enum options
			// one straight with key=>string (rokvyroby), other also with useless category data key=>object (auto_znacka)
			option_value(item)
			{
				let key = Object.keys(item)[0];
				let value = item[key];
				if (typeof value === 'string') {
					return key;
				} else if (typeof value === 'object') {
					return value.value;
				}
			},
			option_label(item)
			{
				let key = Object.keys(item)[0];
				let value = item[key];
				if (typeof value === 'string') {
					return value;
				} else if (typeof value === 'object') {
					return value.label;
				}
			},
		},
		computed: {
			select_2_class_style()
			{
				// does nothing, for now, not implemented yet
				/*if ( field_data.type_settings.select_type === 'simple' ) {
					content = content.replace( '{{select_2_class}}', '' );
					content = content.replace( '{{select_2_style}}', '' );
				} else if ( field_data.type_settings.select_type === 'searchable' ) {
					content = content.replace( '{{select_2_class}}', 'select2_initialize select2' );
					content = content.replace( '{{select_2_style}}', 'style="width:100%"' );
				} else if ( field_data.type_settings.select_type === 'large_ochorenie' ) {
					content = content.replace( '{{select_2_class}}', 'large_select' );
					content = content.replace( '{{select_2_style}}', '' );
				} else {
					// console.log( 'undefined select_type for: ' + field_id );
					content = content.replace( '{{select_2_class}}', '' );
					content = content.replace( '{{select_2_style}}', '' );
				}*/
			},
			disabled()
			{
				if (this.field_data.disabled === 'true') {
					return true;
				}
				return false;
			},
			value:
			{
				get() {
					return State_session_params().get_user_input(this.field_id);
				},
				set(event_value) {
					State_session_params().set_user_input(this.field_id, event_value);
				},
			},
		},
		watch: {
		},
	}
</script>
