<template>
	<Modal_body :close="true" title="Chyba pri výpočte" >

		<p>Kontrolou zadaných údajov sme zistili, že nasledovná hodnota nie je validná:</p>
		<p>
			<span v-for="(value, key) in errors" >
				{{ key }} : {{ value }} <br>
			</span>
		</p>
		<p>token: {{ token }}</p>
		<p>time: {{ time }}</p>
		<p>
			Pre bližšie info o ďalšom postupe nás prosím kontaktuje: <br>
			<a :href="'mailto:' + mail">{{ mail }}</a>
			<br>
			<span v-if="phone">{{ phone }}</span>
		</p>

	</Modal_body>
</template>

<script>
	import Modal_body from '@/components/modals/parts/Modal_body.vue';
	import State_modals from '@/store/State_modals.js';
	import State from '@/store/State.js';
	import Constants from '@/logic/Constants.js';
	import State_session_params from '@/store/State_session_params.js';

	export default {
		props: {
		},
		components: {
			Modal_body,
		},
		emits: [],
		data() {
			return {
			}
		},
		created() {
		},
		methods: {
		},
		computed: {
			errors()
			{
				return State_modals().retrieve_modal_data;
			},
			token()
			{
				 return State.get_session_token();
			},
			time()
			{
				return new Date().toLocaleString();
			},
			mail()
			{
				return Constants.email_address();
			},
			phone()
			{
				if (State_session_params().direct_user_no_agent()) {
					return null;
				}
				return Constants.phone_number(true);
			},
		},
		watch: {
		},
	}
</script>
