<template>
	<select
		:name="field_id"
		:id="field_id"
		v-model="value"
		class="form-control"
	>
		<option
			v-for="(item, key) in field_data.enum"
			:value="option_value(item)"
		>
			{{ option_label(item) }}
		</option>
	</select>
</template>

<script>
	import State_session_params from '@/store/State_session_params.js';

	export default {
		props: {
			field_id: String,
			field_data: Object,
		},
		components: {
		},
		emits: ['validate_global_changed'],
		data() {
			return {
			}
		},
		created() {
		},
		methods: {
			// we have two types of enum options
			// one straight with key=>string (rokvyroby), other also with useless category data key=>object (auto_znacka)
			option_value(item)
			{
				let key = Object.keys(item)[0];
				let value = item[key];
				if (typeof value === 'string') {
					return key;
				} else if (typeof value === 'object') {
					return value.value;
				}
			},
			option_label(item)
			{
				let key = Object.keys(item)[0];
				let value = item[key];
				if (typeof value === 'string') {
					return value;
				} else if (typeof value === 'object') {
					return value.label;
				}
			},
		},
		computed: {
			value:
			{
				get() {
					return State_session_params().get_user_input(this.field_id);
				},
				set(event_value) {
					State_session_params().set_user_input(this.field_id, event_value);
					this.$emit('validate_global_changed');
				},
			},
		},
		watch: {
		},
	}
</script>
